// components/profile/EditProfile.tsx
"use client";

import React, { useEffect, useRef, useState } from "react";
import ReactDOM from "react-dom";

type User = {
  id: string;
  email: string;
  name?: string;
  profile?: any;
};

type Props = {
  open: boolean;
  onClose: () => void;
  user: User | null;
  onSave: (payload: Partial<User & { profile?: any }>) => Promise<boolean> | boolean;
};

export default function EditProfile({ open, onClose, user, onSave }: Props) {
  const [saving, setSaving] = useState(false);
  const [fullName, setFullName] = useState("");
  const [displayName, setDisplayName] = useState("");
  const [gender, setGender] = useState("");
  const [country, setCountry] = useState("");
  const [stateVal, setStateVal] = useState("");
  const [language, setLanguage] = useState("");
  const [timezone, setTimezone] = useState("");
  const primaryRef = useRef<HTMLButtonElement | null>(null);

  // load values from user whenever modal opens or user changes
  useEffect(() => {
    if (!user) return;
    setFullName(user.name ?? "");
    const p = user.profile ?? {};
    setDisplayName(p.displayName ?? "");
    setGender(p.gender ?? "");
    setCountry(p.country ?? "");
    setStateVal(p.state ?? "");
    setLanguage(p.language ?? "");
    setTimezone(p.timezone ?? "");
  }, [user, open]);

  // lock scroll & focus primary save button on open
  useEffect(() => {
    if (!open) return;
    const prev = document.body.style.overflow;
    document.body.style.overflow = "hidden";
    const t = setTimeout(() => primaryRef.current?.focus(), 80);
    return () => {
      document.body.style.overflow = prev;
      clearTimeout(t);
    };
  }, [open]);

  // ESC to close
  useEffect(() => {
    function onKey(e: KeyboardEvent) {
      if (e.key === "Escape") onClose();
    }
    if (open) window.addEventListener("keydown", onKey);
    return () => window.removeEventListener("keydown", onKey);
  }, [open, onClose]);

  if (!open) return null;

  async function submit(e?: React.FormEvent) {
    e?.preventDefault();
    if (!user) return;
    if (!fullName || typeof fullName !== "string") {
      // simple validation
      return;
    }

    setSaving(true);
    try {
      const payload = {
        name: fullName,
        profile: {
          displayName,
          gender,
          country,
          state: stateVal,
          language,
          timezone,
        },
      };

      const ok = await onSave(payload);
      // onSave should return boolean indicating success
      if (!ok) {
        // leave modal open; onSave may show errors
      }
    } finally {
      setSaving(false);
    }
  }

  return ReactDOM.createPortal(
    <div className="fixed inset-0 z-50 flex items-center justify-center p-4">
      <div className="absolute inset-0 bg-black/40" onClick={onClose} aria-hidden />

      <div className="relative z-10 w-full max-w-2xl bg-white rounded-2xl p-6 shadow-lg border">
        <div className="flex items-start justify-between gap-4">
          <div>
            <h3 className="text-lg font-semibold">Edit profile</h3>
            <p className="text-sm text-slate-600 mt-1">Email is read-only. Update other profile fields and save.</p>
          </div>
          <div>
            <button onClick={onClose} className="text-slate-500 hover:text-slate-700">âœ•</button>
          </div>
        </div>

        <form className="mt-6 space-y-4" onSubmit={submit}>
          <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
            <div>
              <label className="text-xs text-slate-600 block">Full name</label>
              <input
                className="mt-1 w-full rounded-lg border px-3 py-2"
                value={fullName}
                onChange={(e) => setFullName(e.target.value)}
                required
              />
            </div>

            <div>
              <label className="text-xs text-slate-600 block">Display name</label>
              <input
                className="mt-1 w-full rounded-lg border px-3 py-2"
                value={displayName}
                onChange={(e) => setDisplayName(e.target.value)}
              />
            </div>

            <div>
              <label className="text-xs text-slate-600 block">Email (read-only)</label>
              <input className="mt-1 w-full rounded-lg border px-3 py-2 bg-slate-50" value={user?.email ?? ""} readOnly />
            </div>

            <div>
              <label className="text-xs text-slate-600 block">Gender</label>
              <select className="mt-1 w-full rounded-lg border px-3 py-2" value={gender} onChange={(e) => setGender(e.target.value)}>
                <option value="">Prefer not to say</option>
                <option value="female">Female</option>
                <option value="male">Male</option>
                <option value="other">Other</option>
              </select>
            </div>

            <div>
              <label className="text-xs text-slate-600 block">Country/Region</label>
              <input value={country} onChange={(e) => setCountry(e.target.value)} className="mt-1 w-full rounded-lg border px-3 py-2" />
            </div>

            <div>
              <label className="text-xs text-slate-600 block">State</label>
              <input value={stateVal} onChange={(e) => setStateVal(e.target.value)} className="mt-1 w-full rounded-lg border px-3 py-2" />
            </div>

            <div>
              <label className="text-xs text-slate-600 block">Language</label>
              <input value={language} onChange={(e) => setLanguage(e.target.value)} className="mt-1 w-full rounded-lg border px-3 py-2" />
            </div>

            <div>
              <label className="text-xs text-slate-600 block">Time zone</label>
              <input value={timezone} onChange={(e) => setTimezone(e.target.value)} className="mt-1 w-full rounded-lg border px-3 py-2" />
            </div>
          </div>

          <div className="flex justify-end gap-3 mt-2">
            <button type="button" onClick={onClose} className="px-4 py-2 rounded-lg border">Cancel</button>
            <button ref={primaryRef} type="submit" disabled={saving} className="px-4 py-2 rounded-lg bg-blue-600 text-white">
              {saving ? "Saving..." : "Save changes"}
            </button>
          </div>
        </form>
      </div>
    </div>,
    document.body
  );
}
