// components/profile/ProfileMenu.tsx
"use client";

import React, { useEffect, useRef, useState } from "react";
import { useRouter } from "next/navigation";

export default function ProfileMenu({ email, avatarUrl }: { email?: string; avatarUrl?: string }) {
  const router = useRouter();
  const [open, setOpen] = useState(false);
  const [confirmOpen, setConfirmOpen] = useState(false);
  const ref = useRef<HTMLDivElement | null>(null);

  useEffect(() => {
    function onDoc(e: MouseEvent) {
      if (!ref.current) return;
      if (!(e.target instanceof Node)) return;
      if (!ref.current.contains(e.target)) setOpen(false);
    }
    document.addEventListener("mousedown", onDoc);
    return () => document.removeEventListener("mousedown", onDoc);
  }, []);

  async function handleLogout() {
    try {
      const res = await fetch("/api/auth/logout", { method: "POST" });
      // ignore response, route clears cookie
      router.replace("/auth/login");
    } catch (err) {
      console.error(err);
    }
  }

  return (
    <div className="relative" ref={ref}>
      <button
        onClick={() => setOpen((v) => !v)}
        className="w-10 h-10 rounded-full bg-slate-100 flex items-center justify-center"
        aria-haspopup="true"
        aria-expanded={open}
      >
        {avatarUrl ? <img src={avatarUrl} alt="avatar" className="w-10 h-10 rounded-full object-cover" /> : <span className="text-sm font-medium">{(email || "U").charAt(0).toUpperCase()}</span>}
      </button>

      {open && (
        <div className="absolute right-0 mt-2 w-48 bg-white rounded-lg shadow-lg border py-2 z-20">
          <button onClick={() => { setOpen(false); router.push("/profile"); }} className="w-full text-left px-3 py-2 text-sm hover:bg-slate-50">View profile</button>
          <button onClick={() => setConfirmOpen(true)} className="w-full text-left px-3 py-2 text-sm hover:bg-slate-50">Logout</button>
        </div>
      )}

      {confirmOpen && (
        <div className="fixed inset-0 z-40 flex items-center justify-center p-4">
          <div className="absolute inset-0 bg-black/40" onClick={() => setConfirmOpen(false)} />
          <div className="relative z-10 w-full max-w-sm bg-white rounded-lg p-4 shadow">
            <h3 className="text-lg font-semibold">Log out?</h3>
            <p className="text-sm text-slate-600 mt-1">Do you want to logout?</p>
            <div className="mt-4 flex justify-end gap-3">
              <button onClick={() => setConfirmOpen(false)} className="px-3 py-2 rounded border">Cancel</button>
              <button onClick={handleLogout} className="px-3 py-2 rounded bg-red-600 text-white">Logout</button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
